.services{
    position:relative;
    width: 100% !important;
    background-color:black;
    
}

.textposition{
    position:absolute;
    left:10px;
    top:10px;
   
}

.imageresize{
    width:60%;
}

.texthere{
    color:white;
    font-size: 80px;
    font-family: Arial, Helvetica, sans-serif;
    position:absolute;
    left:496px;
    top:345px;

}
.quoteStyle{
    color:white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 18px;
    text-align: center;
}

.quote1{
    position:absolute;
    left:24px;
    top:272px;
    width:200px;
    
}
.editCard{
    width: 18rem;
    display:inline-block;
    margin-left: 15px;
}

.editcardimage{
    width: 100%;
    height:160px;
}

@media (max-width: 600px) {
    .texthere{
        color:white;
        font-size: 20px;
        font-family: Arial, Helvetica, sans-serif;
        position:absolute;
        left:150px;
        top:240px;
    }
    .quoteStyle{
        color:white;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-size: 8px;
        position: absolute;
        left: 0px;
        top: 0px;
    }

    .quote1{
        position:absolute;
        left:0px;
        top:205px;
        width:80px;
        
    }

    
    .editCard{
        width: 80%;
        height:450px;
       margin-left: 40px;
        margin-bottom: 30px;
       
    }
}


